<template>
  <div class="blog-post">
    <div class="container">
      <div v-for="post in posts" key="post.key" class="post">
        <span class="id-post">{{ post.key }}</span>
        <h2 class="post-title">{{ post.postTitle }}</h2>
        <span class="date-post">{{ post.postDate }}</span>
        <p class="post-content">
          {{ post.postContent }}
        </p>
        <div class="footer-post">
          <p class="author-name">{{ post.authorName }}</p>
          <p class="cateory-name">{{ post.categroy }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import firebase from "firebase";

  export default {
    name: "blog-post",
    data() {
      return {
        count: 0,
        posts: [],
        ref: firebase.firestore().collection("blog"),
      };
    },
    created() {
      this.ref.onSnapshot((querySnapShot) => {
        this.posts = [];
        querySnapShot.forEach((doc) => {
          this.posts.push({
            key: doc.id,
            postTitle: doc.data().postTitle,
            postContent: doc.data().postContent,
            authorName: doc.data().authorName,
            categroy: doc.data().categroy,
            postDate: doc.data().postDate,
          });
        });
      });
    },
  };
</script>

<style lang="scss" scoped>
  * {
    margin: 0;
    padding: 0;
  }

  .container {
    display: flex;
    flex-direction: column;
    align-items: center;

    .post {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      position: relative;
      width: 500px;
      background-color: #fff;
      padding: 20px;
      margin-bottom: 20px;
      box-shadow: 0px 0px 7px 0px #777;

      .id-post {
        position: absolute;
        top: 0;
        right: 0;
        padding: 10px;
        color: #fff;
        font-weight: bold;
        border-radius: 0 0 0 4px;
        background-color: #577e57;
      }
      .post-title {
        font-size: 40px;
        color: #000;
      }
      .date-post {
        font-size: 12px;
        color: #777;
        padding: 5px 0;
      }
      .post-content {
        color: #333;
        text-align: start;
        padding: 10px 0;
      }
      .footer-post {
        display: flex;
        justify-content: space-between;
        width: 100%;

        .author-name {
          background-color: rgb(68, 68, 68);
          color: #fff;
          padding: 10px 20px;
          border-radius: 5px;
        }
        .cateory-name {
          background-color: rgb(97, 42, 42);
          color: #fff;
          padding: 10px 20px;
          border-radius: 5px;
        }
      }
    }
    @media (max-width: 768px) {
      .post {
        width: 300px;
      }
    }
  }
</style>
