<template>
  <app-header />
  <div class="home">
    <div class="container">
      <div class="card text-center" style="width: 18rem">
        <div class="card-body">
          <h5 class="card-title">User Info</h5>
          <p class="card-text">Hi {{ user.displayName }}</p>
          <p class="card-text">Your Email : {{ user.email }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import AppHeader from "@/components/gloabl/AppHeader.vue";
  import firebase from "firebase";
  import SayHellow from "@/components/gloabl/SayHellow.vue";
  export default {
    name: "Home",
    data() {
      return {
        user: null,
      };
    },
    created() {
      firebase.auth().onAuthStateChanged((users) => {
        if (users) {
          this.user = users;
        } else {
          this.user = null;
        }
      });
    },
    components: {
      AppHeader,
      SayHellow,
    },
    computed: {},
  };
</script>

<style lang="scss" scoped>
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 150px;
    h1 {
      margin-bottom: 50px;
    }
    .card-title {
      font-size: 40px;
      font-weight: bold;
    }
  }
</style>
