<template>
  <app-header />
  <div class="add-post">
    <div class="container">
      <form @submit.prevent="addPostFun">
        <div class="row">
          <div class="col">
            <input
              type="text"
              class="form-control"
              required
              placeholder="Post Title"
              v-model="post.postTitle"
            />
          </div>
          <div class="col">
            <input
              type="text"
              required
              class="form-control"
              placeholder="Author Name"
              v-model="post.authorName"
            />
          </div>
        </div>
        <div class="row">
          <div class="col">
            <input
              type="text"
              required
              class="form-control"
              placeholder="Cateroy"
              v-model="post.categroy"
            />
          </div>
          <div class="col">
            <input
              type="date"
              required
              class="form-control"
              v-model="post.postDate"
            />
          </div>
        </div>
        <div class="mb-3">
          <textarea
            class="form-control"
            id="exampleFormControlTextarea1"
            rows="3"
            required
            placeholder="Post Content"
            v-model="post.postContent"
          ></textarea>
        </div>
        <button @click="timer" type="submit" class="btn btn-primary">
          Publich Post
        </button>
      </form>
    </div>
  </div>
</template>

<script>
  import firebase from "firebase";
  import AppHeader from "@/components/gloabl/AppHeader.vue";
  export default {
    name: "AddPost",
    components: {
      AppHeader,
    },
    data() {
      return {
        post: {},
        ref: firebase.firestore().collection("blog"),
        count: 0,
      };
    },
    methods: {
      addPostFun(evt) {
        this.count += 1;
        this.ref
          .add(this.post)
          .then(() => {
            // this.post.postId = 1;
            this.post.postTitle = "";
            this.post.postContent = "";
            this.post.authorName = "";
            this.post.categroy = "";
            this.post.postDate = "2222";
            this.$router.push("/blog");
          })
          .catch((err) => {
            alert("Error", err);
          });
      },
    },
  };
</script>

<style lang="scss" scoped>
  .container {
    margin-top: 150px;
    text-align: start;
    input {
      margin: 10px 0;
    }
    textarea {
      height: 300px;
      overflow-y: auto;
      resize: none;
    }
    button {
      display: block;
      background-color: #3bb983;
      border: none;
      transition: 0.3s linear;
    }
    button:hover {
      background-color: #212529;
      color: #3bb983;
    }
  }
</style>
