<template>
	<div class="rest-pass">
		<div class="container">
			<form @submit.prevent="restPassword">
				<div>
					<label for="floatingInputGrid">Email address</label>
					<input
						type="email"
						class="form-control"
						id="floatingInputGrid"
						placeholder="name@example.com"
						v-model="email"
					/>
				</div>
				<button type="submit" @click="timer" class="btn btn-primary">
					Send Email
				</button>
			</form>
		</div>
	</div>
</template>
<script>
import firebase from "firebase";
export default {
	name: "ResetPassword",
	data() {
		return {
			email: "",
			message: "",
		};
	},
	methods: {
		restPassword() {
			firebase
				.auth()
				.sendPasswordResetEmail(this.email)
				.then(() => {
					this.$router.push("/done");
				})
				.catch((err) => {
					console.log(err);
				});
		},
	},
};
</script>

<style lang="scss" scoped>
.container {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: start;
	height: calc(100vh - 100px);

	form {
		max-width: 300px;
	}
	label {
		padding-left: 5px;
	}
	input {
		width: 100%;
		margin: 10px 0;
	}
	button {
		background-color: #3bb983;
		border: none;
		transition: 0.3s linear;
	}
	button:hover {
		background-color: #212529;
		color: #3bb983;
	}
}
</style>
