<template>
  <div v class="scroll">
    <button id="up" @click="scrollToTop">
      <fa class="icon-top" icon="angle-up" />
    </button>
  </div>
</template>
<script>
  window.addEventListener("scroll", function () {
    if (window.scrollY >= 50) {
      document.getElementById("up").style.opacity = "1";
    }
    if (window.scrollY <= 500) {
      document.getElementById("up").style.opacity = "0";
    }
  });

  export default {
    name: "ScrollTopButton",
    data() {
      return {};
    },
    methods: {
      scrollToTop() {
        window.scrollTo({
          top: 0,
          behavior: "smooth",
        });
      },
    },
  };
</script>

<style lang="scss" scoped>
  #up {
    width: 50px;
    height: 50px;
    border: none;
    border-radius: 5px;
    background-color: #42b983;
    position: fixed;
    right: 25px;
    bottom: 25px;
    opacity: 0;
    transition: 0.3s linear;
    cursor: pointer;
    user-select: none;
    .icon-top {
      color: #fff;
      font-size: 25px;
    }
  }
  #up:hover {
    background-color: #42b983;
  }
</style>
