<template>
  <div class="sign-up">
    <div class="container">
      <form @submit.prevent="userSignup">
        <div v-show="massErr" class="alert alert-danger" role="alert">
          {{ massErr }}
        </div>
        <div class="col">
          <div>
            <label for="exampleInputName" class="form-label">Name</label>
            <input
              type="text"
              class="form-control"
              id="exampleInputName"
              placeholder="Enter Your Name"
              required
              v-model="user.name"
            />
          </div>
          <div>
            <label for="exampleInputEmail1" class="form-label"
              >Email address</label
            >
            <input
              type="email"
              class="form-control"
              id="exampleInputEmail1"
              placeholder="Enter Your Email"
              required
              v-model="user.email"
            />
          </div>
          <div>
            <label for="password" class="form-label">Password</label>
            <input
              type="password"
              class="form-control"
              id="password"
              aria-describedby="emailHelp"
              placeholder="Enter Your Password"
              required
              v-model="user.password"
            />
          </div>
          <!-- <div>
            <label for="exampleInputPassword1" class="form-label"
              >Confirm Password</label
            >
            <input
              type="password"
              class="form-control"
              id="exampleInputPassword1"
              placeholder="Confirm Your Password"
              v-model="user.password"
            />
          </div> -->
        </div>
        <div class="footer-form">
          <button type="submit" class="btn btn-primary">SignUp</button>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
  import firebase from "firebase";
  export default {
    name: "SignUp",
    data() {
      return {
        massErr: "",
        user: {
          name: "",
          email: "",
          password: "",
        },
      };
    },
    methods: {
      userSignup() {
        firebase
          .auth()
          .createUserWithEmailAndPassword(this.user.email, this.user.password)
          .then((res) => {
            res.user
              .updateProfile({
                displayName: this.user.name,
              })
              .then(() => {
                this.$router.push("/login");
              });
          })
          .catch((edd) => {
            switch (edd.code) {
              case "auth/email-already-in-use":
                this.massErr = "This Email Already Used";
                break;
              case "auth/invalid-email":
                this.massErr = "Invalid Email";
                break;
              case "auth/weak-password":
                this.massErr = "Password Is Soo Weak , Try Agian";
                break;
              default:
                console.log(edd.code);
                break;
            }
          });
      },
    },
  };
</script>

<style lang="scss" scoped>
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 100px);
    text-align: start;

    div {
      margin-bottom: 20px;
    }
    label {
      padding-left: 10px;
    }
    form {
      align-items: flex-start;

      input {
        width: 250px;
      }

      .footer-form {
        width: 100%;
        display: flex;
        justify-content: flex-start;
      }
      a {
        display: inline-block;
        text-decoration: none;
        font-size: 15px;
        padding-left: 10px;
        color: #212529;
        transition: 0.3s linear;
      }
      a:hover {
        color: #3bb983;
      }
      button {
        display: block;
        background-color: #3bb983;
        border: none;
        transition: 0.3s linear;
      }
      button:hover {
        background-color: #212529;
        color: #3bb983;
      }
    }
  }
</style>
